import React, { useRef } from "react";
import { useGLTF } from "@react-three/drei";

export function Foreigner(props) {
  const { nodes, materials } = useGLTF("/glossarySelected/foreigner.glb");
  return (
    <group {...props} dispose={null}>
      <group //position={[-6.97, -1.96, -0.37]}
      >
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere153.geometry}
          material={materials["glass.004"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere153_1.geometry}
          material={materials["glass.001"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere153_2.geometry}
          material={materials.outline}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere153_3.geometry}
          material={materials["glass.005"]}
        />
      </group>
    </group>
  );
}
export default Foreigner;
useGLTF.preload("/glossarySelected/foreigner.glb");
